<template>
    <div>
        <el-form :model="userInfo" label-width="120px" label-position="left" label-suffix=":">
            <el-row>
                <el-col :span="6">
                    <el-form-item label="姓名">{{userInfo.name}}</el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6">
                    <el-form-item label="出生日期">{{userInfo.date}}</el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6">
                    <el-form-item label="邮箱">{{userInfo.email}}</el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6">
                    <el-form-item label="ip">{{userInfo.ip}}</el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    export default {
        activated() {
            this.getDetail()
        },
        data() {
            return {
                userInfo: this.getDefaultUserInfo()
            }
        },
        methods: {
            getDefaultUserInfo() {
                const info = {}
                const fields = ['name', 'date', 'email', 'ip']
                fields.forEach(prop => {
                    info[prop] = undefined
                })
                return info
            },
            getDetail() {
                api.mockDemo.getDetailById({id: this.$route.query.id}).then(res => {
                    if (res.code === 200) {
                        this.userInfo = res.data
                    } else {
                        this.$message.error(res.msg)
                    }
                })
            }
        }
    }
</script>

